<template>
  <section class="blog-post-item">
    <div class="container">
      <TheTitle :theme="theme" :title="post.title || title" />
      
      <div class="wrapper">
        <div class="image-block">
          <img :src="`http://old.phoenixglobaltradings.com/images/${this.post.img}`" :alt="post.title">
        </div>
        <div class="text-block">
          <h3 class="title">
            {{post.title}}
          </h3>
          <p class="text-content">
             {{post.text}}
          </p>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import TheTitle from "../components/The-title"
import axios from "axios"

export default {
  name: "Blog",
  components: {
    TheTitle
  },
  data() {
    return {
      theme: "Main menu / Blog",
      title: "Blog-post",
      postId: this.$route.params.postId,
      post: {}
    };
  },
  methods: {
    async getBlog() {
      const dataRes = await axios(`http://old.phoenixglobaltradings.com/blog/${this.postId}`).then(res => res.data)
      this.post = dataRes
    }
  },
  mounted() {
    this.getBlog()
  },
};
</script>

<style lang="scss">
@mixin media-size($size) {
  @media (max-width: $size) {
    @content;
  }
}

.blog-post-item {
  min-height: 100vh;
  padding: 220px 0 70px;
  // Media
  @include media-size(1230px) {
    padding: 170px 0 30px;
  }
  @include media-size(790px) {
    padding: 150px 0 30px;
  }
  @include media-size(570px) {
    padding: 150px 15px 30px;
  }

  .wrapper{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 100px;
    // Media
    @include media-size(1020px) {
      margin-top: 50px;
    }
    
    .image-block{
      width: 60%;
      height: 500px;
      border-radius: 20px;
      background: #bbbbbb;
      margin: 0 auto;
      box-shadow: -15px 10px 55px #929292ab, 15px 10px 55px #929292ab;
      overflow: hidden;
      // Media
      @include media-size(1230px) {
        width: 80%;
        height: 400px;
      }
      @include media-size(790px) {
        width: 100%;
        height: 400px;
      }
      @include media-size(570px) {
        width: 100%;
        height: 300px;
      }
      
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    
    .text-block{
      width: 40%;
      margin: 0 auto;
      padding: 25px 20px;
      background: #fff;
      border-radius: 20px;
      transform: translateY(-80px);
      box-shadow: 0 50px 50px 10px rgba(29, 109, 195, 0.4);
      // Media
      @include media-size(1230px) {
        width: 60%;
      }
      @include media-size(790px) {
        width: 80%;
      }
      
      .title{
        font-size: 25px;
        position: relative;
        padding-bottom: 15px;
        margin-bottom: 15px;
        color: #555;
        &::after{
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 4px;
          background: linear-gradient(to right, #eb2027, #ba5400, #f7cf24);
        }
        // Media
        @include media-size(570px) {
          font-size: 20px;
        }
      }
      
      .text-content{
        font-size: 20px;
        color: #333;
        // Media
        @include media-size(570px) {
          font-size: 15px;
        }
      }
    }
  }
}
</style>